<template>
	<view>
		<login></login>
		<song-menu :menuList="menuList"></song-menu>
	</view>
</template>

<script>
	import { menusDetails } from '@/http/http.api.js';
	export default {
		data() {
			return {
				menuList: []
			}
		},
		onLoad() {
			if(this.vuex_menuList.length === 0){
				menusDetails().then(res => {
					this.menuList = res.data.list.splice(0, 4);
					this.$u.vuex('vuex_menuList', this.menuList);
				}).catch(e => {
					console.log(e);
				});
			}else{
				this.menuList = this.vuex_menuList;
			}
		}
	}
</script>

